.App {
  text-align: center;
  background: repeating-linear-gradient(rgb(99, 97, 97) , white);
}
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10nv;
  background: rgb(77, 75, 75);
  color:white;

}
.nav-links{
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
li{
  color: white;
}
li:hover {
transform: scale(1.5);
transition-duration: 0.75s;
}
.bar{
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: space-around;
}
.post{
  background: repeating-radial-gradient( rgb(106, 117, 87) , white) ;
  padding: 20px;
}
img{
  height: 200px;
  width: 200px;
}
.postH2{
  color: black;
}
label{
  margin: 5px;
}
.mainPostDiv{
  display: flex;
  overflow: hidden;
  background-color: #ebe4e4;
  color:rgb(0, 0, 0);
  width:500px;
  height: 250px;
  border-radius: 30px;
  border:solid 1px black;
  }
  .picFrame{
      width:170px;
      height: 170px;
      margin-left: 5px;
  margin-top: 30px;
  border-radius: 50%;
  overflow: hidden;
  }
  .roundPic{
  width:100%;
  height: 100%;
  }
  .roundPic:hover {
      -webkit-transform: rotateX(6deg) scale(1.4);
      -moz-transform: rotateX(6deg) scale(1.4);
      -ms-transform: rotateX(6deg) scale(1.4);
      -o-transform: rotateX(6deg) scale(1.4);
      transform:  rotateX(6deg) scale(1.4);
      transition-duration: 0.75s;
      }
  .postHeader{
  text-decoration: none;
  color:#000;
  font-family: cursive;
  margin-left: 8px;
  }
  .header-desc{
      width:130px;
      height: 130px;
      margin-left:15px;
  margin-top:5px;;
  }
  .description{
  border-radius: 8px;
  min-width:250px ;
  max-height: 130px;
  }